.main {
    background: url('@{dir}main.jpg') no-repeat top center;
    background-size: cover;
    padding-bottom: 15px;
    .container {
        position: relative;
    }
    
    .language {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 9;
        li {
            display: inline-block;
            vertical-align: top;
            
            a {
                font-size: 16px;
                color: #353535;
                cursor: pointer;
                line-height: 19px;
                &:hover {
                    color: #fff;
                }

            }
            .active {
                display: inline-block;;
                width: 43px;
                height: 20px;
                line-height: 19px;
                text-align: center;
                background: #abe2ec;
                border-radius: 20px;
                color: #353535;
            }
        }
    }
    h1 {
        margin-top: 50px;
        margin-bottom: 25px;
        @media @mobile {
            text-align: center;
            font-size: 23px;
        }
        @media @tablet { 
            font-size: 50px;
        }
        @media @desktop { 
            text-align: left; 
            font-size: 45px;
        }
        @media @desktop-xl {  
            font-size: 62px;
        }
    }
    h2 {
        font-size: 30px;
        font-weight: 400;
        font-family: 'Ubuntu', sans-serif;
        text-transform: none;
        text-align: left;
        
        color: #3f3f3f;
        @media @mobile {
            font-size: 14px;
            text-align: center;
        }
        @media @tablet { 

            font-size: 32px;
        }
        @media @desktop { 
            text-align: left; 
            font-size: 24px;
            padding-right: 100px;
        }
        @media @desktop-xl {  
            font-size: 30px;
        }
        &:after {
            content: "";
            display: block;
            width: 70px;
            height: 2px;
            background: #3f3f3f;
            
            @media @mobile {
                margin: 0 auto;
                margin-bottom: 22px;
                margin-top: 22px;
            }
            @media @desktop { 
                margin: 0;
                margin-bottom: 22px;
                margin-top: 22px;
                
            }
        }
    }
    h3 {
        
        
        font-weight: 400;
        color: #fff;
        @media @mobile {
            width: 315px;
            margin: 0 auto;
            margin-bottom: 25px;
            text-align: center;
            font-size: 11px;
        }
        @media @tablet { 
            font-size: 18px;
            width: 525px;   
        }
        @media @desktop {     
            margin: 0;
            margin-bottom: 25px;
            text-align: left;    
        }
    }
    .btn-blue {
        @media @mobile {
            width: 315px;
            margin: 0 auto;
            font-size: 12px;
        }
        @media @tablet {
            width: 470px;
            font-size: 16px;
        }
        @media @desktop { 
            margin: 0;
              
        }
    }

    &__slider {
        padding-top: 40px;

        &-item__img {
            @media @mobile {              
                text-align: center;            
            }
            @media @desktop { 
                text-align: center;
                  
            }
            img {
                @media @mobile {              
                    width: 150px;           
                }  
                @media @tablet {
                   width: auto;
                }
            }
        }
        p {
            color: #fff;
            font-size: 18px;
            font-weight: 100;
        }
    }
    .swiper--prev,
    .swiper-container-rtl .swiper--next {
        background-image: url("../images/prev.png");
        right: auto;
        @media @mobile {
            right: 50px;
        }
        @media @tablet {
            right: 50px;
        }
        @media @desktop {

            left: 299px;
        }
        @media @desktop-xl {
            left: 299px;
        }
      &:hover {
        
      }
      &:active {
        
      }

    }
    .swiper--next,
    .swiper-container-rtl .swiper--prev {
        background-image: url("../images/next.png");
        left: auto;
        @media @mobile {
            right: 16px;
        }
        @media @tablet {
            right: 16px;
        }
        @media @desktop {
            right: 16px;
        }
        @media @desktop-xl {
            right: 16px;
        }
      &:hover {
        
      }
      &:active {
       
      }
    }

    .swiper--prev,
    .swiper--next {
      position: absolute;
      margin-top: -22px;
      z-index: 10;
      cursor: pointer;
      -moz-background-size: 36px 45px;
      -webkit-background-size: 36px 45px;
      background-size: 36px 45px;
      background-position: center;
      background-repeat: no-repeat;
    
        @media @mobile {
            top: 70%;
            width: 36px;
            height: 45px;
        }
        @media @tablet {
            top: 70%;
            width: 36px;
            height: 45px;
        }
        @media @desktop {
            top: 70%;
        }
        @media @desktop-xl {
            top: 70%;
        }
    }
    .swiper-pagination-bullet {
      width: 16px;
      height: 16px;
      display: inline-block;
      border-radius: 100%;
      background: #fff;
      z-index: 10;
      opacity: 1;
      
    }
    .swiper-pagination-bullet-active {
        background: #fc9401;
    }
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
        bottom: 10px;
        left: 0;
        width: 100%;

    }
    
}