.form {
   
    
    
    @media @mobile {
        padding-top: 40px;
        background: #fdfbef;
    }
    @media @tablet { 
        margin-top: -55px;
        padding-top: 130px;
    }
    @media @desktop {  
         background: url('@{dir}form.png') no-repeat top center;  
    }
    h3 {
        font-family: 'Roboto Slab', serif;
       
        font-weight: 300;
        margin-bottom: 30px;
        @media @mobile {
            font-size: 20px;
            text-align: center;
        }
        @media @tablet { 
            font-size: 23px;
            text-align: left;
        }
        @media @desktop {  
            font-size: 30px;    
        }
        @media @desktop-xl {  
             font-size: 36px;
        }
        span {
            text-transform: uppercase;
            font-weight: 400;
        }  
    }
    p {
            @media @mobile {
                color: #f08501;
                font-size: 13px;
                text-align: center;
                margin-bottom: 10px;
            }
            @media @tablet { 
                text-align: left;
                font-size: 16px;
                margin-bottom: 0;
            }
            @media @desktop {  
                font-size: 20px;    
            }
        }
    .main-form {
        padding-top: 10px;
        padding-left: 20px;
        @media @mobile {
            
            text-align: center;
        }
        @media @tablet { 
            text-align: left;
           
        }
    }
    .input {
            
            height: 53px;
            background: #ffdc73;
            border: 1px solid #fef3b3;
            color: #3f3f3f;
            padding: 18px 18px;
            outline: none;
            overflow: hidden;
            line-height: 52px;
            border-radius: 5px;
            font-size: 16px;
            font-weight: 400;

            @media @mobile {
                margin: 0 auto;
                width: 268px;
                margin-bottom: 18px;
            }
            @media @tablet { 
                margin: 0;
                width: 268px;
                margin-bottom: 18px;
            }
 
        }

        .input::-webkit-input-placeholder {
            font-family: 'Ubuntu', sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #3f3f3f !important;
        }
        .input::-moz-placeholder {
            font-family: 'Ubuntu', sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #3f3f3f !important;
        }
        .input:-moz-placeholder {
            font-family: 'Ubuntu', sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #3f3f3f !important;
        }
        .input:-ms-input-placeholder {
            font-family: 'Ubuntu', sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #3f3f3f !important;
        }
        .input:focus::-webkit-input-placeholder,
        textarea:focus::-webkit-input-placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
        .input:focus::-moz-placeholder,
        textarea:focus::-moz-placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
        .input:focus:-moz-placeholder,
        textarea:focus:-moz-placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
        .input:focus:-ms-input-placeholder,
        textarea:focus:-ms-input-placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
        .btn-blue {
            width: 270px;
            height: 53px;
            line-height: 52px;
            font-size: 14px;
            
            @media @mobile {
                margin: 0 auto;
                margin-bottom: 80px;
                
            }
            @media @tablet { 
                margin: 0;
                margin-bottom: 80px;
                
            }
        }
}