.delivery {
    background: url('@{dir}delivery.jpg') no-repeat top center;
    background: cover;
    padding-top: 80px;
    text-align: center;
    h2 {
        @media @mobile {
            font-size: 18px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
    &__block {
        position: relative;
        @media @desktop {  
                &:hover {
                    .ukraine {
                        background: url('@{dir}map-hover.png') no-repeat top center;
                    }                    
                }                  
            }
        
        .ukraine {
            margin-bottom: 40px;
            @media @mobile {
                width: 100%;
                height: auto;
            }
            @media @tablet { 
                width: 720px;
                height: 488px;
            }
            @media @desktop {  
                width: 870px;
                height: 590px;
                              
            }
            
        }
        &-map {

            
           
            .first {
                position: absolute;
                
                height: 105px;
                width: 51px;
                @media @mobile {
                    top: -5%;
                    left: 40%;
                }
                @media @tablet { 
                    top: 47px;
                    left: 320px;
                }
                @media @desktop {  
                    top: 72px;
                    left: 430px;
                    
                }
                @media @desktop-xl {  
                    top: 72px;
                    left: 540px;

                }
                img {
                    margin-bottom: 10px;
                    &:hover {
                        transform: scale(1.3);
                        transition-timing-function: cubic-bezier(.47,2.02,.31,-0.36);
                    }
                }
                
            }
            .two {
                
                
                text-align: left;
                @media @mobile {
                    width: 315px;
                    margin: 0 auto;
                    margin-bottom: 10px;
                }
                @media @tablet { 
                    width: auto;
                    margin-bottom: 0;
                    position: absolute;
                    top: 183px;
                    left: 70px;
                }
                @media @desktop {  
                    top: 222px;
                    left: 100px;
                    
                }
                @media @desktop-xl {  
                    top: 222px;
                    left: 214px;

                }
                img {
                    margin-right: 20px;
                    display: inline-block;
                    vertical-align: top;
                }
                p {
                    display: inline-block;
                    vertical-align: top;
                    
                    padding-top: 10px;
                    @media @mobile {
                        font-size: 15px;
                    }
                    @media @tablet { 
                        font-size: 14px;
                    }
                    @media @desktop {  
                        font-size: 20px;
                    }
                }
            }
            .three {
               
                
               text-align: left;
                
                @media @mobile {
                    width: 315px;
                    margin: 0 auto;
                }
                @media @tablet { 
                    width: auto;
                    text-align: left;
                     position: absolute;
                    top: 183px;
                    left: 418px;
                }
                @media @desktop {  
                    top: 222px;
                    left: 507px;
                    
                }
                @media @desktop-xl {  
                    top: 222px;
                    left: 614px;

                }
                img {
                    margin-right: 20px;
                    display: inline-block;
                    vertical-align: top;
                }
                p {
                    display: inline-block;
                    vertical-align: top;
                    padding-top: 10px;
                    @media @mobile {
                        font-size: 15px;
                    }
                    @media @tablet { 
                        font-size: 14px;
                    }
                    @media @desktop {  
                        font-size: 20px;
                    }
                }
            }
            

        }
    }

}