.reviews {
    padding-bottom: 80px;
    padding-top: 60px;
    h2 {
        @media @mobile {
            font-size: 20px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
    &__slider-item {
        @media @mobile {
            text-align: center;
        }
        @media @tablet { 
            text-align: left;
        }
        &__img {
            display: inline-block;
            vertical-align: top;
            @media @mobile {
                margin-bottom: 10px;
            }
            @media @tablet { 
                width: 30%;
                margin-bottom: 0;
                padding-top: 50px;
            }
            @media @desktop {  
                width: 30%;
            }
            @media @desktop-xl {  
                width: auto;
            }
            img {
                width: 100%;
            }
        }
        &__text {
            display: inline-block;
            vertical-align: top;
            padding-bottom: 25px;
            
            @media @mobile {
                
            }
            @media @tablet {
                padding-left: 20px; 
                width: 58%;
            }
            @media @desktop {  
                width: 58%;
            }
            @media @desktop-xl {  
                width: 662px;
            }
            h3 {
                font-family: 'Roboto Slab', serif;
                
                font-weight: 300;
                
                @media @mobile {
                    font-size: 16px;
                    margin-bottom: 15px;
                }
                @media @tablet { 
                    font-size: 14px;
                    margin-bottom: 15px;
                }
                @media @desktop {  
                    font-size: 26px;
                    margin-bottom: 15px;
                }
                @media @desktop-xl {  
                    font-size: 30px;
                    margin-bottom: 25px;
                }
            }
            h4 {
                
                font-weight: 700;
                
                @media @mobile {
                    font-size: 15px;
                    margin-bottom: 15px;
                }
                @media @tablet { 
                    font-size: 14px;
                    margin-bottom: 15px;
                }
                @media @desktop {  
                    font-size: 18px;
                    margin-bottom: 15px;
                }
                @media @desktop-xl {  
                    font-size: 18px;
                    margin-bottom: 25px;
                }
            }
            p {
                @media @mobile {

                    font-size: 14px;
                    padding: 0 10px 15px;
                }
                @media @tablet { 
                    padding: 0;
                    font-size: 12px;
                    margin-bottom: 7px;
                }
                @media @desktop {  
                    font-size: 14px;
                }
                @media @desktop-xl {  
                    font-size: 18px;
                }
            }
        }
    }
    .swiper--prev,
    .swiper-container-rtl .swiper--next {
        background-image: url("../images/prev2.png");
        right: auto;
        @media @mobile {
            right: 50px;
        }
        @media @tablet {
            right: 50px;
        }
        @media @desktop {

            right: 50px;
        }
        @media @desktop-xl {
            right: 50px;
        }
      &:hover {
        
      }
      &:active {
        
      }

    }
    .swiper--next,
    .swiper-container-rtl .swiper--prev {
        background-image: url("../images/next2.png");
        left: auto;
        @media @mobile {
            right: 16px;
        }
        @media @tablet {
            right: 16px;
        }
        @media @desktop {
            right: 16px;
        }
        @media @desktop-xl {
            right: 16px;
        }
      &:hover {
        
      }
      &:active {
       
      }
    }

    .swiper--prev,
    .swiper--next {
      position: absolute;
      margin-top: -22px;
      z-index: 10;
      cursor: pointer;
      -moz-background-size: 36px 45px;
      -webkit-background-size: 36px 45px;
      background-size: 36px 45px;
      background-position: center;
      background-repeat: no-repeat;
    
        @media @mobile {
            top: 90%;
            width: 36px;
            height: 45px;
            display: none;
        }
        @media @tablet {
            top: 90%;
            width: 36px;
            height: 45px;
            display: block;
        }
        @media @desktop {
            top: 90%;
        }
        @media @desktop-xl {
            top: 90%;
        }
    }
    .swiper-pagination-bullet {
      width: 13px;
      height: 13px;
      display: inline-block;
      border-radius: 100%;
      border: 1px solid #cacaca;
      background: #fff;
      z-index: 10;
      opacity: 1;
      
    }
    .swiper-pagination-bullet-active {
        background: #abe2ec;
        border: none;
    }
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
        bottom: 0px;
        left: 0;
        width: 100%;

    }
    
}