.products {
    padding-top: 75px;
    .container {
        position: relative;
    }
    
    h2 {
        @media @mobile {
            text-align: center !important;
            font-size: 20px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            text-align: left;
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
    &__section {
        text-align: center;
        margin-bottom: 20px;
        .btn-white {
            
            @media @mobile {
                display: inline-block;
                vertical-align: top;
                width: 150px;
                height: 50px;
            }
            @media @tablet { 
                width: 270px;
                height: 50px;
            }

        }
        .btn-white1 {
            
            @media @mobile {
                margin-right: 10px;
            }
            @media @tablet { 
                margin-right: 25px;
            }
        }
    }
    .swiper-slide {
        
        img {
            @media @mobile {
                width: 90px;
                height: 159px;
            }
            @media @tablet { 
                width: auto;
                height: auto;
            }
        }
        h3 {
            @media @mobile {
                font-size: 12px;
            }
            @media @tablet { 
                font-size: 18px;
            }
        }
        p {

        }
        
    }
    .adults__slider {
        text-align: center;
        background: #fff;
        
        @media @mobile {
            width: 315px !important;
        }
        @media @tablet { 
            width: 700px !important;
        }
        @media @desktop {  
            width: 900px !important;
        }
        @media @desktop-xl {  
            width: 1050px !important;
        }
    }
    .children__slider {
        text-align: center;
      
         background: #fff;
        @media @mobile {
            width: 315px; 
        }
        @media @tablet { 
            width: 700px;
        }
        @media @desktop {  
            width: 900px;
        }
        @media @desktop-xl {  
            width: 1050px;
        }  
    }

    .swiper--prev,
    .swiper-container-rtl .swiper--next {
        background-image: url("../images/products-prev.png");
        right: auto;
        @media @mobile {
            display: none;
        }
        @media @tablet {
            display: block;
            left: 0px;
        }
        @media @desktop {

            left: 0px;
        }
        @media @desktop-xl {
            left: 0px;
        }
      &:hover {
        background-image: url("../images/products-prev-hover.png");
      }
      &:active {
        
      }

    }
    .swiper--next,
    .swiper-container-rtl .swiper--prev {
        background-image: url("../images/products-next.png");
        left: auto;
        @media @mobile {
            display: none;
        }
        @media @tablet {
            display: block;
            right: 0px;
        }
        @media @desktop {
            right: 0px;
        }
        @media @desktop-xl {
            right: 0px;
        }
      &:hover {
        background-image: url("../images/products-next-hover.png");
      }
      &:active {
       
      }
    }

    .swiper--prev,
    .swiper--next {
      position: absolute;
      margin-top: -22px;
      z-index: 10;
      cursor: pointer;
      -moz-background-size: 50px 50px;
      -webkit-background-size: 50px 50px;
      background-size: 50px 50px;
      background-position: center;
      background-repeat: no-repeat;
    
        @media @mobile {
            top: 42%;
            width: 30px;
            height: 30px;
            border-radius: 5px;
        }
        @media @tablet {
            top: 42%;
            
        }
        @media @desktop {
            width: 50px;
            height: 50px;
            top: 42%;
            border-radius: 0px;
        }
        @media @desktop-xl {
            top: 42%;
        }
    }
    .swiper-pagination-bullet {
      width: 16px;
      height: 16px;
      display: inline-block;
      border-radius: 100%;
      background: #fff;
      z-index: 10;
      opacity: 1;
      
    }
    .swiper-pagination-bullet-active {
        background: #fc9401;
    }
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
        bottom: 10px;
        left: 0;
        width: 100%;

    }
    .btn-blue {
        width: 370px;
        height: 60px;
        margin: 0 auto;
        margin-top: 40px;
        margin-bottom: 50px;
        @media @mobile {
            width: 315px;
            height: 60px;
        }
        @media @tablet {
            width: 370px;
            height: 60px;
            
        }
    }

    .children {
        position: absolute; 
        
        @media @mobile {
            top: 223px;
        	left: 50%;
        	margin-left: -155px;
        }
        @media @tablet {
            top: 206px;
        	left: 12px;
        	margin-left: 0;
            
        }
        @media @desktop {
            top: 216px;
        	left: 22px;
        }
        @media @desktop-xl {
            top: 220px;
        	left: 59px;
        }

        
    }
    
}