.footer {
    padding-top: 15px;
    padding-bottom: 15px;
    &__logo {
        
        @media @mobile {
            width: 315px;
            margin: 0 auto;
            
        }
        @media @tablet { 
            display: inline-block;
            vertical-align: top;
            width: auto;
            margin: 0 ;
        }
        p {
            display: inline-block;
            vertical-align: top;
            color: #353535;
            
            
            @media @mobile {
                font-size: 12px;
                margin-left: 10px;
                padding-top: 10px;
            }
            @media @tablet { 
                display: none;
            }
            @media @desktop {
                padding-top: 5px;
                margin-left: 20px; 
                font-size: 14px; 
                display: inline-block;
                    
            }
            @media @desktop-xl {  
                

            }
        }
    }
    &__center {
        text-align: center;
        
        
        @media @mobile {
            padding-top: 15px;
        }
        @media @tablet {
            margin-left: 85px; 
            display: inline-block;
            vertical-align: top;
            padding-top: 5px;
        }
        p {
            font-size: 14px;
        }
        a {
            font-size: 14px;
            color: #0b77ca;
            text-decoration: underline;
        }
    }
    &__right {
        

        @media @mobile {
            text-align: center;
            padding-top: 15px;
        }
        @media @tablet { 
            display: inline-block;
            vertical-align: top;
            text-align: right;
            padding-top: 5px;
            float: right;
        }
        p {
            font-size: 14px;
        }
        a {
            font-size: 14px;
            color: #0b77ca;
        }
    }
}