.remodal-first {
    
    text-align: center;
    background: #ffdc73;
    border-radius: 5px;
    
    @media @mobile {
         width: 300px !important;
         padding-top: 20px;
    }
    @media @tablet {
         padding-top: 40px;
         width: 370px !important;
         height: 485px !important;
    }
    @media @desktop {  
        width: 370px !important;
        height: 485px !important;
    }
    @media @desktop-xl {  
        width: 370px !important;
        height: 485px !important;
    }
    
    h3 {
        font-family: 'Roboto Slab', serif;
        font-weight: 300;
        color: #3f3f3f;
        margin-bottom: 10px;
        @media @mobile {
            font-size: 23px;
        }
        @media @tablet {
            font-size: 30px;
        }
    }
    p {
        text-align: left;
        color: #3f3f3f;
        margin-bottom: 20px;

        @media @mobile {
            font-size: 14px;
            padding-left: 25px;
            padding-right: 25px;
        }
        @media @tablet {
            padding-left: 50px;
            padding-right: 25px;
            font-size: 18px;
        }
    }
    .text2 {
        padding-right: 20px;
    }
    .text3 {
        padding-left: 45px;
        padding-right: 14px;
        @media @mobile {
            padding-left: 35px;
            padding-right: 14px;
        }
        @media @tablet {
            padding-left: 45px;
            padding-right: 14px;
        }
    }

    .input {
            
            height: 53px;
            background: #fff;
            border: 1px solid #fef3b3;
            color: #3f3f3f;
            padding: 18px 18px;
            outline: none;
            overflow: hidden;
            line-height: 52px;
            border-radius: 5px;
            font-size: 16px;
            font-weight: 400;

            @media @mobile {
                margin: 0 auto;
                width: 268px;
                margin-bottom: 18px;
            }
            @media @tablet { 
                margin: 0;
                width: 268px;
                margin-bottom: 18px;
            }
 
        }

        .input::-webkit-input-placeholder {
            font-family: 'Ubuntu', sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #3f3f3f !important;
        }
        .input::-moz-placeholder {
            font-family: 'Ubuntu', sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #3f3f3f !important;
        }
        .input:-moz-placeholder {
            font-family: 'Ubuntu', sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #3f3f3f !important;
        }
        .input:-ms-input-placeholder {
            font-family: 'Ubuntu', sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #3f3f3f !important;
        }
        .input:focus::-webkit-input-placeholder,
        textarea:focus::-webkit-input-placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
        .input:focus::-moz-placeholder,
        textarea:focus::-moz-placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
        .input:focus:-moz-placeholder,
        textarea:focus:-moz-placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
        .input:focus:-ms-input-placeholder,
        textarea:focus:-ms-input-placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
        .btn-blue {
            width: 270px;
            height: 53px;
            line-height: 52px;
            font-size: 14px;
            
            @media @mobile {
                margin: 0 auto;
                margin-bottom: 20px;    
                
                
            }
            @media @tablet { 
                
                
                
            }
        }
}





