.advantages {
    background: #f7f7f7;
    padding-top: 80px;
    h2 {
        @media @mobile {
            font-size: 20px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
    .row {
        margin: 0;
        @media @mobile {
            
        }
        @media @tablet { 
            background: url('@{dir}advantages.jpg') no-repeat top center;
        }
    }
    .stool {
        
        padding: 0;
        img {
            width: 100%;
        }
    }
    &__item {
        position: relative;
        @media @mobile {
            padding: 0;
        }
        @media @tablet { 
            padding-top: 80px;
            padding-bottom: 75px;
        }
        @media @desktop {  
            padding-top: 100px;
            padding-bottom: 40px; 
        }
        @media @desktop-xl {  
            padding-top: 80px;
            padding-bottom: 60px;
        }
        h3 {
            
            
            @media @mobile {
                text-align: center;
                margin-bottom: 10px;
            }
            @media @tablet { 
                position: absolute;
                top: 20px;
                left: 100px;
                margin-bottom: 0;
            }
            @media @desktop {  
                left: 150px;
            }
            @media @desktop-xl {  
                
            }
        }
        li {
            font-size: 18px;
            font-weight: 400;
            margin-bottom: 30px;
            &:last-child {
                margin-bottom: 0;
            }
            @media @mobile {
                font-size: 14px;
            }
            @media @tablet { 
                font-size: 13px;
            }
            @media @desktop {  
                font-size: 14px;
                
            }
            @media @desktop-xl {  
                font-size: 18px;
            }
            img {
                width: 28px;
                height: 28px;
                margin-right: 10px;
            }
        }
        .left {
            
            @media @mobile {
                background: #e0e0e0;
                padding-top: 20px;
                padding-bottom: 20px;
                padding-left: 20px;
                padding-right: 20px;
            }
            @media @tablet { 
                padding-top: 0px;
                padding-bottom: 0px;
                padding-left: 0;
                padding-right: 0;
                width: 240px;
            }
            @media @desktop {  
                width: 350px;  
            }
            @media @desktop-xl {  
                width: 370px;
            }
            // li {
            //     background: url('@{dir}left.png') no-repeat left center;
            // }
        }
        .right {
            @media @mobile {
                padding-top: 20px;
                padding-bottom: 20px;
                padding-left: 20px;
                padding-right: 20px;
                background: #54e4ef;
            }
            @media @tablet { 
                padding-top: 0px;
                padding-bottom: 0px;
                padding-right: 0;
                padding-left: 35px;
                background: none;

            }
            @media @desktop {  
                padding-left: 0;  
            }
        }
    }
}