.contacts {
    background: #abe2ec;
    
    position: relative;
    @media @mobile {
        height: 485px;
    }
    @media @tablet {
        height: 570px;
    }
    .container {
        padding: 0;
    }
    &__map {
        
        @media @mobile {
            display: none;
        }
        @media @tablet {
            display: block;
            position: absolute;
            right: 50%;
            height: 570px;
            width: 1075px;
        }
        @media @desktop {
            right: 40%;
        }
        @media @desktop-xl {
            width: 1225px;
            height: 570px;
        }
        img {
            width: 100%;

            height: 100%;
        }
    }
    &__block {
        @media @mobile {
            text-align: center;
            background: #abe2ec;
            padding-top: 20px;
            padding-bottom: 20px;
            padding-top: 50px;
        }
        @media @tablet {
            text-align: left;
            position: absolute;
            height: 570px; 
            padding-left: 50px;
            left: 50%;
            padding-top: 50px;
            padding-bottom: 0;
        }
        @media @desktop {
            padding-left: 100px;
            left: 60%;
        }
        h3 {
            font-family: 'Roboto Slab', serif;
            
            font-weight: 300;
            color: #fff;
            
            @media @mobile {
                font-size: 30px;
                margin-bottom: 20px;
            }
            @media @tablet {
                margin-bottom: 40px;
                font-size: 36px;
            }
        }
        ul {
            margin-bottom: 40px;
            li {
                font-size: 18px;
                font-weight: 400;
               
                @media @mobile {
                     margin-bottom: 10px;
                }
                @media @tablet {
                     margin-bottom: 20px;
                }
            }
        }
        h4 {
            font-family: 'Roboto Slab', serif;
            font-size: 30px;
            font-weight: 300;
            color: #fff;
            
            @media @mobile {
                 margin-bottom: 10px;
            }
            @media @tablet {
                 margin-bottom: 30px;
            }
        }
        p {

        }
        .btn-blue {
            width: 270px;
            height: 50px;
            background: #ffdc73;
            line-height: 50px;
            font-size: 14px;
            
            @media @mobile {
                 margin: 0 auto;
                 margin-top: 20px;
            }
            @media @tablet {
                margin: 0;
                margin-top: 20px;
                margin-bottom: 30px;
            }
            &:hover {
                background: #90701a;
                color: #fff !important;
            }
        }
    }
}