.system {
    
    padding-bottom: 40px;
    @media @mobile {
        padding-top: 40px;
    }
    @media @tablet { 
        padding-top: 90px;
    }
    h2 {
        @media @mobile {
            font-size: 20px;
        }
        @media @tablet { 
            font-size: 23px;
        }
        @media @desktop { 
            font-size: 30px;
        }
        @media @desktop-xl {  
            font-size: 36px;
        }
    }
    &__item {
        &-mg {
            padding-top: 80px;
        }
        p {
            display: inline-block;
            vertical-align: top;
            @media @mobile {
                font-size: 15px;
                padding-left: 15px;
            }
            @media @tablet { 
                font-size: 10px;

            }
            @media @desktop {  
                font-size: 15px;
            }
            @media @desktop-xl {  
                font-size: 18px;
            }
        }
        .hvr-underline-from-left {
            overflow: visible;
            &:before {
                background: #abe2ec;
                margin-bottom: -5px;
            }
        }
        .left2 {
            &:before {
                background: #ffdc73;
            }
        }
        .left3 {
            &:before {
                background: #01b111;
            }
        }
        .left4 {
            &:before {
                background: #ff0006;
            }
        }
        .left5 {
            &:before {
                background: #ff7c00;
            }
        }
        .left6 {
            &:before {
                background: #06b1ef;
            }
        }
        &-block {
            position: relative;
            @media @mobile {
                width: 305px;
                margin: 0 auto;
                margin-bottom: 15px;
            }
            @media @tablet { 
                width: auto;
                margin: 0;
                margin-bottom: 115px;
            }
            // &:hover {
