                    height: 577px;
                }
                @media @desktop-xl { 
                    width: 354px;
                    height: 681px;
                }
            }
        }
        &-line {
            position: absolute;
            @media @mobile {
                display: none;
            }
            @media @desktop-xl { 
                display: block;
            }
        }
        &-line1 {
