@dir: 						    	"../images/";
@dir-fonts: 				  		"../fonts/";

@html-size-base: 					10px;

@fs: 						      	1.8rem;
@fss: 						    	2rem;
@fsb: 						    	1.6rem;
@h1: 						      	6.2rem;
@h2: 						      	3.6rem;
@h3: 						      	2rem;
@h4: 						      	2rem;



@lh: 						      	1.3;

@ff1: 								'Ubuntu', sans-serif;
@ff2: 								'Roboto Slab', serif;

@black: 							#3f3f3f;
@grey: 								#525150;


@text-color: 						@black;
@text-margin: 						0 0 1rem;

@mobile:      ~"only screen and (min-width: 320px)";
@tablet:      ~"only screen and (min-width: 768px)";
@desktop:     ~"only screen and (min-width: 992px)";
@desktop-xl:  ~"only screen and (min-width: 1200px)";


@import url(https://fonts.googleapis.com/css?family=Ubuntu:400,300,300italic,400italic,500,500italic,700,700italic&subset=latin,cyrillic);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,100,300,700);
