.video {
    
    background: url('@{dir}video.jpg') no-repeat top center;
    background: cover;
     @media @mobile {
         padding-top: 40px;
        padding-bottom: 40px;
    }
    @media @tablet { 
        padding-top: 90px;
        padding-bottom: 90px;
    }

    h2 {
        @media @mobile {
            font-size: 18px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
    &__block {
        
        // margin: 0 auto;
        // @media @mobile {
        //     width: 100%;
        //     height: 100%;
        // }
        // @media @tablet { 
        //     width: 730px;
        //     height: 398px;
        // }
        // @media @desktop {  
        //     width: 770px;
        //     height: 420px;
            
        // }
        @media @desktop {   
            left: 50%;
            margin-left: -385px; 
        }
        position: relative; 
        
        height: 0; 
        overflow:hidden;
        @media @mobile {
           padding-bottom: 56.25%; 
        }
        @media @desktop {  
           width: 770px;
           height: 420px;
            padding-bottom: 37.25%;       
        }
        @media @desktop-xl{  
            
        }
        iframe, object, embed {
            position: absolute; 
            top: 0; 
            left: 0; 
            
            @media @mobile {
                width: 100%;
                height: 100%;
            }
            @media @tablet { 
                width: 730px;
                height: 398px;
            }
            @media @desktop {  
                width: 770px;
                height: 420px;
                           
            }
        }


        .youtube {
            background-position: center;
            background-repeat: no-repeat;
            position: relative;
            display: block;
            overflow: hidden;
            transition: all 200ms ease-out;
            cursor: pointer;
            margin: 0px auto 0 auto;
            @media @mobile {
                width: 300px;
                height: 200px;
            }
            @media @tablet { 
                width: 730px;
                height: 398px;
            }
            @media @desktop {  
                width: 770px;
                height: 420px;
                           
            }

            .play {
                background: url('@{dir}play.png') no-repeat center center;
                background-size: 64px 64px;
                position: absolute;
                height: 100%;
                width: 100%;
                opacity: .8;
                filter: alpha(opacity=80);
                transition: all 0.2s ease-out;
                
                &:hover {
                    opacity: 1;
                    filter: alpha(opacity=100);
                }
            }
            // iframe {
            //   margin: 40px auto 0 auto;
            // }
        }
        .youtube1 {
            @media @mobile {
                display: block !important;
            }
            @media @tablet { 
                
            }
            @media @desktop {  
                display: none !important;
                           
            } 
        }
        .youtube2 {
            @media @mobile {
                display: none !important;
            }
            @media @tablet { 
                
            }
            @media @desktop {  
                display: block !important;
                           
            } 
        }

    }
}