.certificates {
    padding-top: 65px;
    padding-bottom: 30px;
    background: #ffdc73;
    h2 {
        @media @mobile {
            font-size: 18px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
    &__slider-item {
        
        text-align: center;
        &__img-1 {
            
            @media @mobile {
                padding-top: 46px;
            }
            @media @tablet {
                padding-top: 46px;
                padding-bottom: 60px;
            }
            @media @desktop {
                padding-top: 61px;
                padding-bottom: 80px;
            }
            @media @desktop-xl {
                padding-top: 79px;
                padding-bottom: 98px;
            }
        }
        &__img {
            @media @mobile {
                width: 270px;
                margin: 0 auto;
                margin-bottom: 20px;
            }
            @media @tablet {
                width: auto;
                margin: 0;
                margin-bottom: 20px;
            }
            
            img {
                width: 100%;
            }

        }
        p {
            text-align: left;
            @media @mobile {
                text-align: center;
            }
            @media @tablet {
                text-align: left;
                font-size: 14px;
            }
            @media @desktop {
                font-size: 15px;
            }
            @media @desktop-xl {
                font-size: 18px;
            }
        }
    }

    // &-img {
        
    //     @media @tablet { 
    //         padding-top: 78px;
    //         padding-bottom: 97px;
            
    //     }
    // }
    // .row {
    //     text-align: center;
    //     img {
    //         margin-bottom: 20px;
    //     }
    //     p {
    //         text-align: left;
    //         width: 270px;
    //         margin: 0 auto;
    //         margin-bottom: 20px;
    //     }
    //     .mb {

    //     }
    
    // }
    .swiper-wrapper {
        margin-bottom: 35px;
    }
    .swiper-pagination-bullet {
      width: 13px;
      height: 13px;
      display: inline-block;
      border-radius: 100%;
      border: 1px solid #cacaca;
      background: #fff;
      z-index: 10;
      opacity: 1;
      
    }
    .swiper-pagination-bullet-active {
        background: #abe2ec;
        border: none;
    }
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
        bottom: -4px;
        left: 0;
        width: 100%;

    }

    
    
}