.prevention {
    
    background: url('@{dir}prevention.jpg') no-repeat top center;
    background-size: cover;
    @media @mobile {
        padding-top: 35px;
        padding-bottom: 30px;
    }
    @media @tablet { 
        padding-top: 75px;
        padding-bottom: 70px;
    }
    h2 {
        
        margin-bottom: 30px;
        @media @mobile {
            font-size: 27px;
            text-align: center;
        }
        @media @tablet { 
            font-size: 36px;
            text-align: left;
        }
        @media @desktop { 
            
        }
        &:after {
            display: none;
        }
    }
    h3 {
        
        margin-bottom: 50px;
        @media @mobile {
            width: 315px;
            text-align: center;
            margin: 0 auto;
            margin-bottom: 10px;
            font-size: 14px;
        }
        @media @tablet { 
            width: 630px;
            text-align: left;
            font-size: 20px;
            margin: 0;
            margin-bottom: 10px;
        }
    }
    &__item {
        text-align: center;
        img {
            @media @mobile {
                width: 170px;
                height: 170px;
            }
            @media @tablet { 
                width: 108px;
                height: 108px;
            }
            @media @desktop { 
                width: 140px;
                height: 140px;
            }
            @media @desktop-xl {  
                width: 170px;
                height: 170px;
            }
        }
        p {
            
            @media @mobile {
                font-size: 23px;
                margin-bottom: 10px;
                margin-top: 10px;
            }
            @media @tablet { 
                margin-top: 35px;
                font-size: 13px;
                margin-bottom: 0;
            }
            @media @desktop { 
                font-size: 16px;
            }
            @media @desktop-xl { 
                font-size: 18px;
            }
        }
    }
    
}