.materials {
    padding-top: 70px;
    h2 {
        @media @mobile {
            font-size: 20px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
    .hover {
    	
        &:hover {
            .materials__item-img {
                display: none;
                
            }
            .materials__item-hover {
                display: block;
               
                
            }
        }
    }
    &__item {
        position: relative;
        margin-bottom: 30px;
        padding: 0;
        @media @mobile {
            text-align: center;
        }
        @media @tablet { 
            text-align: left;
        }

        &-block {
            padding: 0;
            margin-bottom: 30px;
        }
        &-img {
            img {
                
            }
        }
        &-hover {
            display: none;
            img {

            }
            .item-text {
                position: absolute;
                
                padding-right: 66px;
                @media @mobile {
                    top: 10px;
                    left: 50%;
                    margin-left: -120px;
                    width: 300px;       
                }
                @media @tablet { 
                    width: auto;
                    margin-left: 0;
                    top: 30px;
                    left: 60px;
                }
                @media @desktop {  
                    top: 30px;
                    left: 60px;
                }
                @media @desktop-xl {  
                    top: 55px;
                    left: 60px;

                }
                h3 {
                    font-family: 'Roboto Slab', serif;
                    font-weight: 300;
                    color: #fff;
                    
                    @media @mobile {
                        font-size: 15px;
                        margin-bottom: 10px;
                    }
                    @media @tablet { 
                        font-size: 21px;
                        margin-bottom: 25px;
                    }
                    @media @desktop {  
                        font-size: 24px;
                    }
                }
                p {
                    
                    font-weight: 400;
                    color: #fff; 
                    @media @mobile {
                        
                        font-size: 11px;
                    }
                    @media @tablet { 
                        font-size: 10px;
                    }
                    @media @desktop {  
                        font-size: 15px;
                    }
                    @media @desktop-xl {  
                        font-size: 18px;

                    }
                }
            }
            .item-text5 {
                
                @media @mobile {
                    top: 130px;
                    left: 50%;
                    margin-left: -120px;
                    width: 300px;
                }
                @media @tablet { 
                    width: auto;
                    margin-left: 0;
                    top: 130px;
                    left: 60px;
                }
                @media @desktop {  
                    top: 130px;
                    left: 60px;
                }
                @media @desktop-xl {  
                    top: 220px;
                    left: 60px;

                }
            }
        }
    }
    &__item1 {
        
    }
    &__item2 {
        
        @media @mobile {
            
        }
        @media @tablet { 
            padding-left: 20px;
        }
    }
    &__item5 {
        @media @mobile {
            
        }
        @media @tablet { 
            padding-left: 20px;
        }
    }
    .widht {
        @media @mobile {
            width: 315px;
            height: 167px;
        }
        @media @tablet { 
            width: 348px;
            height: 185px;
        }
        @media @desktop {  
            width: 455px;
            height: 247px;
        }
        @media @desktop-xl {  
            width: 570px;
            height: 303px;

        }
    }
    .widht3 {
        @media @mobile {
            width: 315px;
            height: 141px;
        }
        @media @tablet { 
            width: 400px;
            height: 179px;
        }
        @media @desktop {  
            width: 530px;
            height: 237px;
        }
        @media @desktop-xl {  
            width: 670px;
            height: 300px;

        }
    }
    .widht5 {
        @media @mobile {
            width: 315px;
            height: 422px;
        }
        @media @tablet { 
            width: 289px;
            height: 387px;
        }
        @media @desktop {  
            width: 377px;
            height: 505px;
        }
        @media @desktop-xl {  
            width: 470px;
            height: 630px;

        }
    }
    &__footer {
        
        
        margin-bottom: 30px;
        @media @mobile {
            background: url('@{dir}materials__footer.jpg') no-repeat right center;
            padding: 35px 0 35px 0px;
        }
        @media @tablet { 
            padding: 35px 0 35px 300px;
        }
        @media @desktop {  
            background: url('@{dir}materials__footer.jpg') no-repeat top center;
            padding: 35px 0 35px 400px;
        }
        @media @desktop-xl {  
            padding: 35px 0 35px 400px;
        }
        h4 {
           
            @media @mobile {
                text-align: center;
                font-size: 14px;
            }
            @media @tablet { 
                text-align: left;
                font-size: 16px;
            }
            @media @desktop {  
                font-size: 25px;
            }
            @media @desktop-xl {  
                font-size: 30px;
            }
            
            
        }
    }
}