.figures {
	background: url('@{dir}figures.jpg') no-repeat top center;
    background-size: cover;
    
    @media @mobile {
        padding-top: 40px;
    	padding-bottom: 40px;
    }
    @media @tablet { 
        padding-top: 90px;
    	padding-bottom: 80px;
    }
    h2 {
    	@media @mobile {
            font-size: 25px;
        }
        @media @tablet { 
            font-size: 28px;
        }
        @media @desktop { 
            font-size: 36px;
        }
    	&:after {
    		background: url('@{dir}after2.png') no-repeat top center;
    	}
    }
    &__item {
    	text-align: center;
    	@media @mobile {
            margin-bottom: 15px;
        }
        @media @tablet { 
            margin-bottom: 0;
        }
    	h3 {
	    	font-size: 72px;
			font-weight: 400;
			font-family: 'Roboto Slab', serif;
			@media @mobile {
            	font-size: 50px;
	        }
	        @media @tablet { 
	            font-size: 50px;
	        }
	        @media @desktop { 
	            font-size: 72px;
	        }
			&:after {
	            content: "";
	            display: block;
	            width: 20px;
	            height: 2px;
	            background: #3f3f3f;
	            margin: 0 auto;
	            margin-top: 25px;
	            margin-bottom: 25px;

	        }
	    }
	    p {
			@media @mobile {
            	font-size: 15px;
	        }
	        @media @tablet { 
	            font-size: 15px;
	        }
	        @media @desktop { 
	            font-size: 18px;
	        }
	    }
    }
    
}