p {
	font-size: @fs; 
	line-height: @lh;
	color: @grey; 
	margin: 0;
	font-family: @ff1;
}
h1 {
	font-family: @ff2;
	font-size: @h1; 
	font-weight: 700; 
	line-height: 1.2; 
	color: #fff; 
	margin: 0;
	text-align: left;	
	text-transform: uppercase;
}
h2 {
	font-family: @ff2;
	font-size: @h2; 
	font-weight: 300; 
	line-height: @lh; 
	color: @text-color; 
	text-align: center;
	margin: 0;
	&:after {
        content: "";
        display: block;
        width: 170px;
        height: 19px;
        background: url('@{dir}after.png') no-repeat top center;
        margin: 0 auto;
        margin-top: 25px;
        margin-bottom: 60px;
    }
	
}
h3 {
	font-family: @ff1;
	font-size: @h3; 
	font-weight: 400; 
	line-height: @lh; 
	color: @text-color; 
	
	
	margin: 0;
}
h4 {
	font-family: @ff1;
	font-size: @h4; 
	font-weight: 400; 
	line-height: @lh; 
	color: @text-color;
	margin: 0;
	
}

a {
	 
	
	&:hover {
		text-decoration: none;
	}
}
ul {
	padding: 0;
	list-style-type: none;
    margin: 0;
}
.btn-blue {
	width: 470px;
    height: 60px;
    color: #023943 !important;
    background: #69c7d8;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 60px;
   

    &:hover {
    	background: #44a8c3;

    	
    }
}
.btn-white {
	width: 270px;
    height: 50px;
    color: #3f3f3f !important;
    background: #fff;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 50px;
    border: 1px solid #ababab;
    outline: none;
    &:hover {
    	background: #90701a;
    	border: none;
    	color: #fff !important;
    }
}
.active {
	background: #ffdc73;
	border: none;
	color: #7f5f00;
	&:hover {
    	background: #90701a;
    	border: none;
    	color: #fff !important;
    }
}
