.header {
	padding-top: 15px;
	
	
	&__block {
		padding-bottom: 15px;
		border-bottom: 1px solid #efefef;
	}
	&__logo {
		
		@media @mobile {
            width: 315px;
            margin: 0 auto;
        }
        @media @tablet { 
            display: inline-block;
			vertical-align: top;
			width: auto;
            margin: 0 ;
        }
		p {
			display: inline-block;
			vertical-align: top;
			
			
			
			@media @mobile {
	            font-size: 12px;
	            margin-left: 10px;
	            padding-top: 10px;
	        }
	        @media @tablet { 
	            display: none;
	        }
	        @media @desktop {
	        	padding-top: 5px;
	        	margin-left: 20px; 
	        	font-size: 16px; 
	        	display: inline-block;
	                
	        }
	        @media @desktop-xl {  
	            

	        }
		}
	}
	.center {
		
		@media @mobile {
            display: none;
        }
        @media @tablet { 
            
        }
        @media @desktop {  
        	display: inline-block;
            margin-left: 20px;
            
        }
        @media @desktop-xl {  
            margin-left: 175px;

        }
	}
	h3 {
		
		font-size: 24px;
		
		padding-top: 5px;
		@media @mobile {
            display: block;
            text-align: center;
            margin-bottom: 10px;
        }
        @media @tablet { 
        	text-align: left;
        	display: inline-block;
			vertical-align: top;
            margin-left: 100px;
            margin-bottom: 0;
        }
        @media @desktop {  
            margin-left: 20px;
            
        }
        @media @desktop-xl {  
            margin-left: 100px;

        }
	}
	.btn-blue {
		
		width: 270px;
		height: 40px;
		font-size: 14px;
		color: #7f5f00 !important;
		line-height: 40px;
		text-transform: none;
		background: #ffdc73;
		
		@media @mobile {
            display: block;
            margin: 0 auto;
        }
        @media @tablet { 
        	display: inline-block;
			vertical-align: top;
			margin: 0;
			float: right;
        }
		&:hover {
			background: #7f5f00;
			color: #fff !important;
		}
	}
	.background {
		background: #FAFAFA url('@{dir}icon-menu.png') no-repeat 10px 11px;
	}
	.background2 {
		background: #FAFAFA;
	}
	.nav {
        display: block;
        padding-top: 20px;
        @media @mobile {
            width: 220px;
            position: relative;
            min-height: 40px;
            margin-left: 15px;
            margin-right: 10px;
            z-index: 10; 
            margin: 0 auto; 
            margin-bottom: 10px;         

        }
        @media @tablet {
        	margin: 0;
            width: auto;
            margin-left: 0px;
            margin-right: 0px;
            margin-bottom: 0px;  
        }
        @media @desktop {
            
        }
        @media @desktop-xl {
            margin-left: 90px;
            margin-right: 90px;
        }
        ul {
            margin: 0;
            @media @mobile {
                width: 220px;
                min-height: 35px;
                padding: 5px 0;
                position: absolute;
                top: 0;
                left: 0;
                border: solid 1px #aaa;
                background: #FAFAFA url('@{dir}icon-menu.png') no-repeat 10px 11px;
                border-radius: 5px;
                box-shadow: 0 1px 2px rgba(0,0,0,.3);
                 /*
                &:hover {
                    width: 135px;
                    background: #FAFAFA url('@{dir}icon-menu.png') no-repeat 10px 11px;
                }
                */
            }
            @media @tablet {
                width: auto;
                padding: 0;
                position: relative;
                border: none; 
                background: transparent;
                box-shadow: 0 0px 0px rgba(0,0,0,.3);
                &:hover {
                    width: auto;
                    background: transparent;
                }
            }
            @media @desktop {
                
                
               
            }
            @media @desktop-xl {
                
            }
            /*
            &:hover li {
                @media @mobile {
                    display: block;
                    margin: 0 0 5px;
                }
                @media @tablet {

                }
                @media @desktop {
                    display: inline-block;
                    margin-right: 24px;
                }
                @media @desktop-xl {

                }
                
            }
            */
            .current {
            	@media @mobile {
            		padding-top: 25px;
            	}
                   
                @media @tablet {
                	padding-top: 0px;
                }
            }
            li {
                display: inline-block;
                vertical-align: top; 
                &:last-child {
                	margin-right: 0;
                }
                
                @media @mobile {
                    display: none; /*   <li>  */
                    margin: 0;
                    margin-bottom: 5px;
                    padding-left: 20px;
                }
                @media @tablet {
                	margin-bottom: 0px;
                    padding-left: 0px;
                	margin-right: 10px;
                	display: inline-block;
                }
                @media @desktop {
                    
                    margin-right: 50px;
                }
                @media @desktop-xl {
					margin-right: 59px;
                }
                a {
                    @media @mobile {
                    display: block;
                    
                    text-align: left;
                    color: #000;
                    }
                    @media @tablet {
						font-size: 13px;
						display: inline-block;
                    }
                    @media @desktop {
                        font-size: 14px;
                        padding: 0;
                        &:hover {
	                        color: #208493;
	                        -webkit-transform: scale(1.2);
					        -ms-transform: scale(1.2);
					        transform: scale(1.2);
					        transition: 0.5s;
					        
	                    }
                    }
                    @media @desktop-xl {

                    }
                    
                    &:hover {
                        color: #208493;
                        

                    }

                }
            }
             
        }

    }
}