.processes {
    padding-top: 80px;
    padding-bottom: 30px;
    h2 {
        @media @mobile {
            font-size: 18px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
    &__item {
        position: relative;
        
        @media @mobile {
            margin-bottom: 20px;
        }
        @media @tablet { 
            margin-bottom: 0px;
        }
    }
    &__item1 {
         @media @desktop-xl { 
            &:after {
                content: "------------";
                display: block;
                letter-spacing: 3px;
                position: absolute;
                left: 194px;
                top: 122px;
                color: #fff;
                font-weight: 500;     
            }
         }
    }
    &__item2 {
        @media @desktop-xl {  
            &:after {
                content: "------------";
                display: block;
                letter-spacing: 3px;
                position: absolute;
                left: 194px;
                top: 122px;
                color: #fff;
                font-weight: 500;     
            }
            &:before {
                content: "------------";
                display: block;
                letter-spacing: 3px;
                position: absolute;
                right: 194px;
                top: 122px;
                color: #fff;
                font-weight: 500;     
            }
        }
        
        @media @tablet { 
            margin-bottom: 60px;
        }
    }
    &__item3 {
        @media @desktop-xl {   
           
            &:after {
                content: "------------";
                display: block;
                letter-spacing: 3px;
                position: absolute;
                left: 194px;
                top: 122px;
                color: #fff;
                font-weight: 500;     
            }
            &:before {
                content: "------------";
                display: block;
                letter-spacing: 3px;
                position: absolute;
                right: 194px;
                top: 122px;
                color: #fff;
                font-weight: 500;     
            }
        }
    }
    &__item4 {
        @media @desktop-xl { 
            &:before {
                content: "------------";
                display: block;
                letter-spacing: 3px;
                position: absolute;
                right: 194px;
                top: 122px;
                color: #fff;
                font-weight: 500;     
            }
        }
    }

    .row {
        text-align: center;

        img {
            margin-bottom: 25px;

        }
        .round {
            
        }
        p {
            text-align: left;
            width: 270px;
            margin: 0 auto;
        }
        .btn-blue {
            width: 270px;
            height: 55px;
            font-size: 14px;
            margin: 0 auto;
            margin-top: 25px;
            line-height: 54px;
        }
    }
}