.statistics {
	
	@media @mobile {
        padding-top: 40px;
		padding-bottom: 50px;
    }
    @media @tablet { 
        padding-top: 80px;
		padding-bottom: 100px;
    }
    h2 {
        @media @mobile {
            font-size: 20px;
        }
        @media @tablet { 
            font-size: 25px;
        }
        @media @desktop {  
            font-size: 33px;
            
        }
        @media @desktop-xl {  
            font-size: 36px;

        }
    }
	
	&__item {
		
		
		&-img {
			position: relative !important;
			width: 270px;
			@media @mobile {
                margin: 0 auto;
                margin-bottom: 20px;
            }
            @media @desktop-xl { 
                margin: 0;
                
            }

			img {
				width: 100%;
			}
		}
		&-block-1 {
		background: url('@{dir}statistics-1.png') no-repeat top center;
		}
		&-block-2 {
			background: url('@{dir}statistics-2.png') no-repeat top center;
		}
		&-block-3 {
			background: url('@{dir}statistics-3.png') no-repeat top center;
		}
		&-block-4 {
			background: url('@{dir}statistics-4.png') no-repeat top center;
		}
		&-block {
			
			width: 270px;
			height: 395px;
			padding: 90px 18px 0 20px;
			text-align: left;
			@media @mobile {
                margin: 0 auto;
                margin-bottom: 20px;   
            }
            @media @desktop-xl { 
                margin: 0;  
            }
            transition: all 0.5s ease-in-out;
			-webkit-transition: all 0.5s ease-in-out;
			-ms-transition: all 0.5s ease-in-out;
            &:hover {
				
				
				-webkit-transform: scale(1.3);
		        -ms-transform: scale(1.3);
		        transform: scale(1.3);
				// -webkit-transition-timing-function: ;
				// -ms-transition-timing-function: ease-in-out;
				// transition-timing-function: ease-in-out;
				position: relative;
				z-index: 1;
				
            }

			h3 {
				font-size: 72px;
				font-weight: 100;
				font-family: 'Roboto Slab', serif;
				color: #fff;
				&:after {
		            content: "";
		            display: block;
		            width: 20px;
		            height: 1px;
		            background: #ffdc73;
		            margin: 15px 0 20px;
		        }
			}
			p {
				font-size: 18px;
				font-weight: 300;
				color: #fff;
			}
		}
	}
}